@mixin mobile{
    @media screen and (max-width:480px){
        @content;
        
    }
}
@mixin tablet{
    @media screen and (max-width:768px){
        @content;
        
    }
}
@mixin leptop{
    @media screen and (max-width:1200px){
        @content;
        
    }
}



.formContainer{
    background-color: rgb(147, 232, 238);
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    
    .fromWrapper{
        background-color: whitesmoke;
        padding: 20px 60px;
        border-radius: 13px;
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 10px; 
        .logo{
            color: rgb(91, 100, 228);
            font-weight: bold;
            font-size: 25px;
        }
        .title{
            color: rgb(91, 100, 228);
            font-size: 15px;
        }
        form{
            display: flex;
            flex-direction: column;
            gap: 15px;

            input{
                padding: 14px;
                border: none;
                width: 250px;
                border-bottom: 1px solid rgb(147, 232, 238);
                &::placeholder{
                    color: rgb(175,175,175);
                }
            }
            button{
                background-color: rgb(91, 100, 228);
                color: white;
                padding: 10px;
                font-weight: bold;
                border: none;
                cursor: pointer;
            }
            label{
                display: flex;
                align-items: center;
                gap: 10px;
                color:rgb(121, 121, 230);
                font-size: 15px;
                cursor: pointer;

                img{
                    width: 30px;
                }
            }
        }
        p{
            color: rgb(121, 121, 230);
            font-size: 15px;
            margin-top: 8px;
        }
    }
   
}

.home{
    background-color: rgb(121, 153, 224);
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;


    .container{
        border: 1px solid white;
        border-radius: 10px;
        width: 65%;
        height: 80%;
        display: flex;
        overflow: hidden;
        @include tablet{
            width: 90%;
        }

        .sidebar{
            flex: 1;
            background-color:  rgb(23, 28, 71);
            position: relative;

            .navbar{
                display: flex;
                align-items: center;
                background-color: rgb(88, 123, 238);
                height: 40px;
                padding: 9px;
                justify-content: space-between;
                color: #dddd;


                .logo{
                    font-weight: bold;
                    @include tablet{
                        display: none;
                    }

                }
                .user{
                    display: flex;
                    gap: 10px;
                }
                img{
                    background-color: #ddddf7;
                    height: 24px;
                    width: 24px;
                    border-radius: 50%;
                    object-fit: cover;
                }

                button{
                    background-color: #4c4cf0;
                    color: #ddddf7;
                    font-size: 14px;
                    border: none;
                    cursor: pointer;
                    @include tablet{
                        position: absolute;
                        bottom: 10px;
                    }
                }

                
            }
            
            .search{
                border-bottom: 1px solid gray;

                .searchFrom{
                    padding: 10px;

                    input{
                      background-color: transparent;
                      border: none;
                      color: white;
                      outline: none;

                      &::placeholder{
                        color: lightgray;
                      }
                    }

                }
            }
            .userChat{
                padding: 10px;
                display: flex;
                align-items: center;
                gap: 10px;
                color: white;
                cursor: pointer;

                &:hover{
                    background-color:  rgb(18, 27, 112);

                }
                 
                img{
                    height: 50px;
                    width: 50px;
                    border-radius: 50%;
                    object-fit: cover;
                }

                .userChatInfo{
                    span{
                        font-size: 15px;
                        font-weight: 500;
                    }
                    p{
                        font-size: 12px;
                        color: lightgray;
                    }
                }
            }
        }
        .chat{
            flex: 2;

            .chatInfo{
                height: 38px;
                background-color: #5275d8;
                display: flex;
                align-items: center;
                justify-content: space-between;
                padding: 10px;
                color: lightgray;
            }
            .chatIcon{
                display: flex;
                gap: 10px;

                img{
                    height: 24px;
                    cursor: pointer;
                }
            }
            .messages{
                background-color: #ddddf7;
                padding: 10px;
                height: calc(100% - 136px);
                overflow: scroll;

                .message{
                    display: flex;
                    gap: 20px;
                    margin-bottom: 20px;
                   
                    .messageInfo{
                        display: flex;
                        flex-direction: column;
                        color: grey;
                       
                        font-weight: 300;
                     
                      
                        img{
                            width: 40px;
                            height: 40px;
                            border-radius: 50%;
                            object-fit: cover;
                        }
                    }
                    .messageContent{
                        max-width: 80%;
                        display: flex;
                        flex-direction: column;
                        gap: 10px;


                        p{
                            background-color: white;
                            padding: 10px 20px;
                            border-radius: 0px 10px 10px 10px;
                            max-width: max-content;
                        }
                        img{
                            width: 50%;
                        }
                    }
                    &.owner{
                        flex-direction: row-reverse;

                        .messageContent{

                            align-items: flex-end;


                          p{
                             background-color: #5275d8;
                              color: white;
                             border-radius: 10px 0px 10px 10px;
                          }
                    }
                    }
                }
            }
            .input{
                height: 38px;
                background-color: white;
                padding: 10px;
                display: flex;
                align-items: center;
                justify-content: space-between;


                input{
                    width: 100%;
                    border: none;
                    outline: none;
                    color: #4c4cf0;
                    font-size: 18px;


                    &::placeholder{
                        color: lightgray;
                    }
                }
                .send{
                    display: flex;
                    align-items: center;
                    gap: 10px;
                 

                    img{
                        height: 24px;
                        cursor: pointer;
                    }
                     button{
                        border: none;
                        padding: 10px 15px;
                        color: white;
                        background-color: #5275d8;
                        cursor: pointer;
                     }
                }
                
            }
        }
    }
}